<template>
  <div class="case_detail">
    <div class="header">
      <a class="item"
        @click="changeInfo(item.value)"
        :class="{'active': item.value == infoType}"
        v-for="item in infoList">
        {{item.label}}
      </a>
    </div>
    <div class="content">
      <div class="left">
        <baseInfo v-if="infoType == 1"></baseInfo>
        <proposerInfo v-if="infoType == 2"></proposerInfo>
        <respondentInfo v-if="infoType == 3"></respondentInfo>
        <requestReply v-if="infoType == 4"></requestReply>
        <hearCase v-if="infoType == 5"></hearCase>
        <sendInfo v-if="infoType == 6"></sendInfo>
        <awardInfo v-if="infoType == 7"></awardInfo>
      </div>
      <div class="right">
        <commissionInfo></commissionInfo>
        <operateMenu class="mgt10"></operateMenu>
      </div>
    </div>
    
  </div>
</template>

<script>
import commissionInfo from './commission_info'
import operateMenu from './operate_menu'
import baseInfo from './base_info'
import proposerInfo from './proposer_info'
import respondentInfo from './respondent_info'
import requestReply from './request_reply'
import hearCase from './hear_case'
import sendInfo from './send_info'
import awardInfo from './award_info'
export default {
  name: 'case_detail',
  components: {
    commissionInfo,
    operateMenu,
    baseInfo,
    proposerInfo,
    respondentInfo,
    requestReply,
    hearCase,
    sendInfo,
    awardInfo,
  },
  data () {
    return {
      infoList: [
        {value: 1, label: '基本信息'},
        {value: 2, label: '申请方'},
        {value: 3, label: '被申请方'},
        {value: 4, label: '请求和答辩(反请求和答辩)'},
        {value: 5, label: '仲裁审理'},
        {value: 6, label: '送达'},
        {value: 7, label: '裁决书'}
      ],
      infoType: 1,
    }
  },
  mounted () {
    
  },
  methods: {
    // 切换查看信息
    changeInfo (val) {
      this.infoType = val
      console.log(val)
    }
  }
}
</script>
<style lang="less" scoped>
.case_detail {
  .header {
    padding: 0 20px;
    width: 100%;
    height: 64px;
    line-height: 64px;
    background: #fff;
    border-radius: 8px;
    .item {
      margin-left: 20px;
      display: inline-block;
      padding: 0 20px;
      height: 36px;
      line-height: 36px;
      font-size:18px;
      font-family:PingFangSC-Medium,PingFang SC;
      font-weight:500;
      color: #708094;
      background: #EFF6FF;
      border-radius: 4px;
      user-select: none;
      &:first-child {
        margin-left: 0;
      }
      &:visited {
        color: red;
      }
    }
    .active {
      background: #3296FA;
      color: #fff;
    }
  }
  .content {
    height: 800px;
    margin-top: 10px;
    // border: 1px solid black;
    display: flex;
    .left {
      width: calc(100% - 385px)
      // flex: 1;
      // background: blue;
    }
    .right {
      margin-left: 10px;
      flex-basis: 375px;
      min-width: 375px;
      // background: yellow;
    }
  }
}
.mgt10 {
  margin-top: 10px;
}
</style>
