<template>
  <div class="base_info">
    <div class="info">
      <myTitle txt="基本信息"></myTitle>
      <div class="content">
        <span class="edit_icon" @click="btnOperate(1)"></span>
        <itemInfo class="mgb5 mgl50" :label="'仲裁机构'" :value="'XX仲裁委'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'案件进度'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'申请时间'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'案件编号'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'案号'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'纠纷金额'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'仲裁费'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'申请人'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'代理人'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'被申请人'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'案由'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
        <itemInfo class="mgb5 mgl50" :label="'使用程序'" :value="'案件进度'" :labelWidth="'200px'"></itemInfo>
      </div>
    </div>
    <div class="process">
      <myTitle txt="案件进度信息"></myTitle>
      <Table stripe
          :columns="columns"
          :data="bodyList">
          <template slot-scope="{ row }" slot="name">
            <span>{{ row.name }}</span>
          </template>
          <template slot-scope="{ row }" slot="name">
            <span>{{ row.name }}</span>
          </template>
          <template slot-scope="{ row }" slot="name">
            <span>{{ row.name }}</span>
          </template>
          <template slot-scope="{ row }" slot="name">
            <span>{{ row.name }}</span>
          </template>
          <template slot-scope="{ row }" slot="name">
            <span>{{ row.name }}</span>
          </template>
        </Table>
    </div>
    <!-- 弹窗 -->
    <popupBox :show="popupShow"
      :width="'720px'" 
      :titleText="'编辑'"
      :footerBtn="false"
      >
        <!-- 弹窗内容 -->
        <Form ref="formValidate"
          class="form" 
          :model="formValidate" 
          :rules="ruleValidate" 
          :label-width="200">
          <FormItem label="仲裁机构">
            <!-- <Input v-model="formValidate.name" placeholder="Enter your name"></Input> -->
            <p>111</p>
          </FormItem>
          <FormItem label="案件进度">
            <p>111</p>
          </FormItem>
          <FormItem label="申请时间">
            <p>111</p>
          </FormItem>
          <FormItem label="案件编号">
            <p>111</p>
          </FormItem>
          <FormItem label="案号">
            <p>111</p>
          </FormItem>
          <FormItem label="纠纷金额" prop="name" style="width: 350px">
            <Input v-model="formValidate.name"
              size="small"
              placeholder="Enter your name"></Input>
          </FormItem>
          <FormItem label="总仲裁费" prop="name" style="width: 350px">
            <Input v-model="formValidate.name"
              size="small"
              placeholder="Enter your name"></Input>
          </FormItem>
          <FormItem label="申请人" prop="name" style="width: 350px">
            <Input v-model="formValidate.name"
              size="small"
              placeholder="Enter your name"></Input>
          </FormItem>
          <FormItem label="代理人" prop="name" style="width: 350px">
            <Input v-model="formValidate.name"
              size="small"
              placeholder="Enter your name"></Input>
          </FormItem>
          <FormItem label="被申请人" prop="name" style="width: 350px">
            <Input v-model="formValidate.name"
              size="small"
              placeholder="Enter your name"></Input>
          </FormItem>
          <FormItem label="案由" prop="city" size="small" style="width: 350px">
              <Select v-model="formValidate.city" placeholder="Select your city">
                  <Option value="beijing">New York</Option>
                  <Option value="shanghai">London</Option>
                  <Option value="shenzhen">Sydney</Option>
              </Select>
          </FormItem>
          <FormItem label="使用程序" prop="city" style="width: 350px">
              <Select v-model="formValidate.city" size="small" placeholder="Select your city">
                  <Option value="beijing">New York</Option>
                  <Option value="shanghai">London</Option>
                  <Option value="shenzhen">Sydney</Option>
              </Select>
          </FormItem>
          <div style="text-align:center">
              <Button type="primary" style="margin-left: 0;"
                @click="handleSubmit('formValidate')">确认</Button>
              <Button @click="handleCancel('formValidate')"
                style="margin-left: 8px">取消</Button>
          </div>
        </Form>
    </popupBox>
  </div>
</template>

<script>
import myTitle from '@/components/common-title/title'
import itemInfo from '@/components/item-info/item-info'
export default {
  name: 'base_info',
  components: {myTitle, itemInfo},
  data () {
    return {
      popupShow: false,
      columns: [
        {
          title: '姓名',
          key: 'name'
        },
        {
          title: '证件类型',
          key: 'name'
        },
        {
          title: '状态',
          key: 'name'
        },
        {
          title: '状态',
          key: 'name'
        },
        {
          title: '状态',
          key: 'name'
        },
        {
          title: '状态',
          key: 'name'
        }
      ],
      bodyList: [
        {
          name: '王小明',
          age: 18,
          birthday: '919526400000',
          address: '北京市朝阳区芍药居'
        },
        {
          name: '张小刚',
          age: 25,
          birthday: '696096000000',
          address: '北京市海淀区西二旗'
        }
      ],
      formValidate: {
          name: '',
          mail: '',
          city: '',
          gender: '',
          interest: [],
          date: '',
          time: '',
          desc: ''
      },
      ruleValidate: {
          name: [
              { required: true, message: 'The name cannot be empty', trigger: 'blur' }
          ],
          mail: [
              { required: true, message: 'Mailbox cannot be empty', trigger: 'blur' },
              { type: 'email', message: 'Incorrect email format', trigger: 'blur' }
          ],
          city: [
              { required: true, message: 'Please select the city', trigger: 'change' }
          ],
          gender: [
              { required: true, message: 'Please select gender', trigger: 'change' }
          ],
          interest: [
              { required: true, type: 'array', min: 1, message: 'Choose at least one hobby', trigger: 'change' },
              { type: 'array', max: 2, message: 'Choose two hobbies at best', trigger: 'change' }
          ],
          date: [
              { required: true, type: 'date', message: 'Please select the date', trigger: 'change' }
          ],
          time: [
              { required: true, type: 'string', message: 'Please select time', trigger: 'change' }
          ],
          desc: [
              { required: true, message: 'Please enter a personal introduction', trigger: 'blur' },
              { type: 'string', min: 20, message: 'Introduce no less than 20 words', trigger: 'blur' }
          ]
      }
    }
  },
  mounted () {
    
  },
  methods: {
    // 按钮操作
    btnOperate(type) {
      this.popupShow = true
    },
    // 弹窗确认
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
          if (valid) {
              this.$Message.success('Success!');
          } else {
              this.$Message.error('Fail!');
          }
      })
    },
    // 弹窗取消 
    handleCancel (name) {
      this.popupShow = false
    }
  }
}
</script>
<style lang="less" scoped>
.base_info {
  .info, .process {
    background: #fff;
    border-radius: 8px;
  }
  .content {
    position: relative;
    padding: 30px 20px;
    .edit_icon {
      position: absolute;
      right: 20px;
      display: inline-block;
      width: 16px;
      height: 16px;
      background-image: url('../../assets/images/edit_icon.png');
      background-size: cover;
      cursor: pointer;
    }
  }
  .process {
    margin-top: 10px;
  }
}
.mgb5 {
  margin-bottom: 5px;
}
.mgl50 {
  margin-left: 15%;
}
// /deep/ .ivu-form-item {
//   margin-bottom: 0;
// }
</style>
<style lang="less">
.ivu-table-wrapper {
  border: none;
}
.ivu-table-header {
}
.ivu-table:before {
  display: none;
}
.ivu-table:after {
  display: none;
}
.ivu-table th {
  color: #9CB2CD;
  // background-color: #fff;
  
}
</style>