<template>
  <div class="proposer_info">
    <div class="proposer">
      <myTitle txt="申请人"></myTitle>
      <div class="content">
        <div class="info">
          <span class="edit_icon" @click="btnOperate(1)"></span>
          <itemInfo class="mgb5" 
            v-for="item in proposerInfo"
            :key="item.value"
            :label="item.label" 
            :value="item.value" 
            :labelWidth="'150px'"></itemInfo>
        </div>
        <div class="pic">
          我是一张图片
          <img src="" alt="">
        </div>
      </div>
    </div>
    <div class="agent">
      <myTitle txt="代理人"></myTitle>
      <div class="content">
        <div class="info">
          <span class="edit_icon" @click="btnOperate(1)"></span>
          <itemInfo class="mgb5" 
            v-for="item in agentInfo"
            :key="item.value"
            :label="item.label" 
            :value="item.value" 
            :labelWidth="'150px'"></itemInfo>
        </div>
        <div class="pic">
          我是一张图片
          <img src="" alt="">
        </div>
      </div>
    </div>
    <!-- 弹窗 -->
    <popupBox :show="popupShow" :width="'720px'" :titleText="'编辑'"
      :footerBtn="false">
        <!-- 弹窗内容 -->
        <div class="tabs">
          <div class="txt">当事人属性：</div>
          <RadioGroup v-model="formValidate.gender">
              <Radio label="1">自然人</Radio>
              <Radio label="2">企业</Radio>
              <Radio label="3">个体户</Radio>
              <Radio label="4">其他组织</Radio>
          </RadioGroup>
        </div>
        <Form ref="formValidate"
          class="form" 
          :model="formValidate" 
          :rules="ruleValidate" 
          :label-width="100">
          </FormItem>
          <div class="main">
            <!-- 使用if根据当事人属性显示隐藏一些内容 -->
            <FormItem label="纠纷金额" prop="name" style="width: 300px">
              <Input v-model="formValidate.name"
                size="small"
                placeholder="Enter your name"></Input>
            </FormItem>
            <FormItem label="案由" prop="city" style="width: 300px">
              <Select v-model="formValidate.city" size="small" placeholder="Select your city">
                  <Option value="beijing">New York</Option>
                  <Option value="shanghai">London</Option>
                  <Option value="shenzhen">Sydney</Option>
              </Select>
            </FormItem>
            <FormItem label="纠纷金额" prop="name" style="width: 300px">
              <Input v-model="formValidate.name"
                size="small"
                placeholder="Enter your name"></Input>
            </FormItem>
            <FormItem label="案由" prop="city" style="width: 300px">
                <Select v-model="formValidate.city" size="small" placeholder="Select your city">
                    <Option value="beijing">New York</Option>
                    <Option value="shanghai">London</Option>
                    <Option value="shenzhen">Sydney</Option>
                </Select>
            </FormItem>
          </div>
          
          <div style="text-align:center">
              <Button type="primary" style="margin-left: 0;"
                @click="handleSubmit('formValidate')">确认</Button>
              <Button @click="handleCancel('formValidate')"
                style="margin-left: 8px">取消</Button>
          </div>
        </Form>
    </popupBox>
  </div>
</template>

<script>
import myTitle from '@/components/common-title/title'
import itemInfo from '@/components/item-info/item-info'
export default {
  name: 'proposer_info',
  components: {myTitle, itemInfo},
  data () {
    return {
      popupShow: false,
      proposerInfo: [
        {label: '企业名称', value: '222'},       
        {label: '证件号码', value: '44'},       
        {label: '法定代表人', value: '44'},       
        {label: '身份证', value: '44'},       
        {label: '联系人', value: '44'},       
        {label: '送达手机', value: '44'},       
        {label: '送达邮箱', value: '44'},       
        {label: '联系地址', value: '44'},       
      ],
      agentInfo: [
        {label: '企业名称', value: '222'},       
        {label: '证件号码', value: '44'},       
        {label: '法定代表人', value: '44'},       
        {label: '身份证', value: '44'},       
        {label: '联系人', value: '44'},       
        {label: '送达手机', value: '44'},       
        {label: '送达邮箱', value: '44'},       
        {label: '联系地址', value: '44'},       
      ],
      formValidate: {
          name: '',
          mail: '',
          city: '',
          gender: '',
          interest: [],
          date: '',
          time: '',
          desc: ''
      },
      ruleValidate: {
          name: [
              { required: true, message: 'The name cannot be empty', trigger: 'blur' }
          ],
          mail: [
              { required: true, message: 'Mailbox cannot be empty', trigger: 'blur' },
              { type: 'email', message: 'Incorrect email format', trigger: 'blur' }
          ],
          city: [
              { required: true, message: 'Please select the city', trigger: 'change' }
          ],
          gender: [
              { required: true, message: 'Please select gender', trigger: 'change' }
          ],
          interest: [
              { required: true, type: 'array', min: 1, message: 'Choose at least one hobby', trigger: 'change' },
              { type: 'array', max: 2, message: 'Choose two hobbies at best', trigger: 'change' }
          ],
          date: [
              { required: true, type: 'date', message: 'Please select the date', trigger: 'change' }
          ],
          time: [
              { required: true, type: 'string', message: 'Please select time', trigger: 'change' }
          ],
          desc: [
              { required: true, message: 'Please enter a personal introduction', trigger: 'blur' },
              { type: 'string', min: 20, message: 'Introduce no less than 20 words', trigger: 'blur' }
          ]
      }
    }
  },
  mounted () {
    
  },
  methods: {
    // 按钮操作
    btnOperate(type) {
      this.popupShow = true
    },
    // 弹窗确认
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
          if (valid) {
              this.$Message.success('Success!');
          } else {
              this.$Message.error('Fail!');
          }
      })
    },
    // 弹窗取消 
    handleCancel (name) {
      this.popupShow = false
    }
  }
}
</script>
<style lang="less" scoped>
.proposer_info {
  .proposer, .agent {
    background: #fff;
    border-radius: 8px;
    .content {
      padding: 30px 20px 50px 20px;
      display: flex;
      .info {
        position: relative;
        flex: 1;
      }
      .pic {
        width: 210px;
        height: 260px;
        border: 1px solid #eee;
      }
    }
  }
  .agent {
    margin-top: 20px;
  }
}
.tabs {
  margin-left: 30px;
  display: flex;
  margin-bottom: 20px;
  .txt {
    padding-top: 2px;
  }
}
.form {
  .main {
    display: flex;
    flex-wrap: wrap;
    // .left, .right {
    //   width: 50%
    // }
  }
}
.mgb5 {
  margin-bottom: 5px;
}
.mgl50 {
  margin-left: 15%;
}
</style>